import React from "react";
import Picture from "./Picture";


class Nav extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [<a>点击</a>],
            flge: false
        };
        this.dianji = this.dianji.bind(this);
    }

    dianji(movie) {
        this.setState({
            flge: true
        })
    }

    render() {
        return (
            <div style={heng}>
                {this.state.data.map((item) => {
                    return (
                        <div key={item} style={cl}>
                            <button style={bt_cl} onClick={() => { this.dianji(item) }}> {item} </button>
                        </div>
                    )
                })}
                {this.state.flge && <Picture />}
            </div>
        )
    }
}


let cl = ({
    color:"red",
    position: 'fixed',
    top: 80,
    left:10,
})

let heng = ({
    display: "flex",
    justifyContent:"space-around",

})


let bt_cl =({
    backgroundColor:"LightSeaGreen",
    borderRadius:5,
    borderColor: "LightSeaGreen",
})


export default Nav;