import React from "react";

function Geren () {
    let t_b = (
        {
            width: '100%',
            height: 30,
            paddingTop:14,
            alignSelf: 'center',
            backgroundColor:'LightGrey'
        }
    )

    let text =({
        marginTop:-10,
    })

    return (
        <div>
            <div>
                <h3 style={t_b}>个人介绍</h3>
            </div>

            <div style={text}>
                <p>
                    我是一个性格开朗的男孩，会在平时的时间里给自己安排时间和伙伴打篮球，偶然也会玩电游。
                    但也会 给自己一 些学习编程的时间，我喜欢编程，喜欢通过学习新的知识来建设自己的网站，
                    一般我会在网 上找一些新知识点来弥补自己，也会看别人写的代码，继续来给自己的程序找问
                    题并完善，也会在能 从别人写的代码中有新的思路和想法。<a>主修课程：web前端</a>
                </p>
            </div>
        </div>
    )
}

export default Geren;