import React from "react";

function Xinx() {
    let xin_xi_l = [
        ["姓名：","王台归"],
        ["出生年月：","2001.01"],
        ["民族：","侗族"],
        ["身高：","164CM"],
        ["电话：","152-8661-7737"]
    ]

    return (
        <div>
            <div><br/>
                {xin_xi_l.map((xin_xi_l,index) => {
                    return (
                        <div key={xin_xi_l[index]}>
                            <span>{xin_xi_l}</span>
                        </div>
                    )
                })}
            </div>
        </div>
    )
}

export default Xinx;