import React from "react";

function Xiaoyuan() {
    let xiao =(
        {
            width: '100%',
            height: 30,
            paddingTop: 14,
            alignSelf: 'center',
            backgroundColor: 'LightGrey',
        }
    )

    let kong = ({
        marginBottom:30,
    })

    let text =({
        marginTop:-10,
    })

    let nei_r = [
        ["2019.10——做过“广汽本田梦想童行项目”，担任前期对接组负责人和讲师。主要是负责找 1000 名学生给他 们上课，在此期间自己和校方进行沟通，并安排好上课时间和具体的流程。"],
        ["2019.10~2020.1——组织学堂学生进行篮球训练和篮球比赛。自己做一套训练流程，在规定的时间组织学堂的 同学进行篮球训练，每个星期负责给球队找一场比赛。"],
        ["2020.01——和老师一起组织过年会，负责协调工作。查看各组人员是否在自己的任务范围内工作，看看有什么 要及时调整的地方，和各组人员进行协调。"],
        ["2019.10 至今在学堂编辑部负责编辑工作。主要自己负责内容方面的工作，选题进行采写。"]
    ]

    let nei_r_1 = [
        ["团队合作较强——和同学一起合作讲过几堂课、和同学合作编程项目"],
        ["容易相处、阳光、可爱、开朗——基本上和谁都能聊得来，不会和同学产生矛盾"],
        ["沟通能力较强——做过对接的工作，当过讲师，也组织同学们的活动"],
        ["自律、有责任心——每天按时出勤，老师布置的任务按时完成"]
    ]

    let t = (
        {
            marginBottom:-15,
        }
    )

    return (
        <div>
            <div style={t}>
                <h3 style={xiao}>校内实践</h3>
                <div>
                    {nei_r.map((nei_r) => {
                        return (
                            <p key={nei_r} style={text}>
                                <a>{nei_r}</a>
                            </p>
                        )
                    })}
                </div>
            </div>
            <div style={kong}></div>

            <div>
                <h3 style={xiao}>自我评价</h3>
                <div>
                    {nei_r_1.map((nei_r_1) => {
                        return (
                            <p key={nei_r_1} style={text}>
                                <a>{nei_r_1}</a>
                            </p>
                        )
                    })}
                </div>
            </div>
        </div>
    )
}

export default Xiaoyuan;