import React from "react";


export default function Picture (){
    let Images = [
        "https://img2.baidu.com/it/u=2999715207,4276127049&fm=26&fmt=auto&gp=0.jpg",
        "https://img0.baidu.com/it/u=3980294509,132844952&fm=26&fmt=auto&gp=0.jpg",
        "https://img0.baidu.com/it/u=22526342,1633902997&fm=26&fmt=auto&gp=0.jpg"
    ]

    let box_fu = ({
        display: 'flex'
    })

    return (
        <div style = {box_fu}>
            {Images.map((Images,index)=>{
                return (
                    <div key={index}>
                        <img style={{ width: 505, height: 600, borderRadius: 10}} src={Images} />
                    </div>

                )
            })}
        </div>

    )
}

