import React from 'react';


export default function Skill(){
    let jiN = [
        ['HTML','70%'],
        ['CSS','60%'],
        ['JavaScript','50%'],
        ['React','20%']
    ]

    let box =({
        width:330,
        height:90,
        backgroundColor:'DarkGray',
        textAlign: 'center',
        borderRadius:10,
        fontSize: 16,
        fontWeight:700,
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center'

    })

    let max_box = ({
        display: 'flex',
        justifyContent: 'center'
    })

    let jinD = ({
        width: 200,
        height: 15,
        backgroundColor: 'Beige',
        textAlign: 'center',
        borderRadius: 10,
    })

    let jinD1 = ({
        width: 150,
        height: 15,
        backgroundColor: 'Aqua',
        textAlign: 'center',
        borderRadius: 10,
    })

    return (
        <div style={max_box}>
            {jiN.map((jiN,index)=>{
                return (
                    <ul key={index}>
                        <div style={box}>
                            <samp >{jiN[0]}:{jiN[1]}</samp>
                            <div style={jinD}>
                                <div style={jinD1}></div>
                            </div>
                        </div>
                    </ul>
                )
            })}
        </div>
    )
}

